
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { NgSelectModule } from '@ng-select/ng-select';
import { SharedModule } from 'src/app/shared/shared.module';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { TableViewModule } from 'src/app/common-module/table-view/table-view.module';
import { SuppliersListComponent } from './suppliers-list/suppliers-list.component';
import { AddSuppliersComponent } from './add-suppliers/add-suppliers.component';
import { SupplierService } from 'src/app/service/suppliers.service';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';

const routes: Routes = [
  {
    path: "",
    component: SuppliersListComponent,
    data: { title: 'suppliers_list' }
  },
  {
    path: "add",
    component: AddSuppliersComponent,
    data: { title: 'add_suppliers' }
  },
  {
    path: 'edit/:id',
    component: AddSuppliersComponent,
    data: { title: 'edit_suppliers' }
  },
]

@NgModule({
  declarations: [SuppliersListComponent, AddSuppliersComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    FormsModule,
    ReactiveFormsModule,
    FormValidationModule,
    SharedModule,
    PaginationModule,
    TableViewModule,
    NgSelectModule

  ], providers: [SupplierService]
})

export class SuppliersModule { }




